                       align=\"center\">\n          <template slot-scope=\"scope\">\n            <el-button type=\"text\"\n                       @click=\"detailsVisible=true,details(scope.row)\">{{scope.row.money}}</el-button>\n          </template>\n        </el-table-column>\n        <el-table-column label=\"创建时间\"\n                         prop=\"create_time\"\n                         align=\"center\">\n\n        </el-table-column>\n\n      </el-table>\n      <el-dialog title=\"详情\"\n                 center\n                 :visible.sync=\"detailsVisible\">\n        <div v-if='detailsList'\n             class=\"details\">\n          <div>\n            <span>支付金额(元) : </span>\n            <span>{{detailsList.money}}</span>\n          </div>\n          <div>\n            <span>支付时间 : </span>\n            <span>{{detailsList.create_time}}</span>\n          </div>\n          <div>\n            <div v-for='(item,index) in detailsList.img'\n                 :key=\"index\"\n                 class=\"images\">\n              <div>\n                <span>\n                  <img :src=\"item.Z_IDcard\"\n                       ref=\"images\" />\n                </span>\n                <p>身份证(正)</p>\n              </div>\n              <div>\n                <span>\n                  <img :src=\"item.F_IDcard\"\n                       ref=\"images\" />\n                </span>\n                <p>身份证(反)</p>\n              </div>\n              <div>\n                <span>\n                  <img :src=\"item.driving\"\n                       ref=\"images\" />\n                </span>\n                <p>驾驶证</p>\n              </div>\n              <div>\n                <span>\n                  <img :src=\"item.credit\"\n                       ref=\"images\" />\n                </span>\n                <p>征信查询授权</p>\n              </div>\n              <div>\n                <span>\n                  <img :src=\"item.warranty\"\n                       ref=\"images\" />\n                </span>\n                <p>授权照片</p>\n              </div>\n            </div>\n          </div>\n\n        </div>\n      </el-dialog>\n      <!-- 分页 -->\n      <div class=\"page_center\"\n           v-show=\"pageCount&&pageCount>1\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n        <!--分页的组件-->\n      </div>\n    </div>\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\nimport Viewer from '@/utils/Viewer'\nexport default {\n  data () {\n    return {\n      //分页数据\n      token: window.sessionStorage.getItem('bbytoken'),\n      curId: 0,\n      threeAuthList: [],\n      seCurId: 0,\n      thCurId: 0,\n      authList: [],\n      page: 1,\n      pageCount: 0,\n      list: [],\n      detailsVisible: false,\n      detailsList: {},\n\n\n    }\n  },\n  methods: {\n    paging (e) {\n      this.page = e\n      this.init()\n    },\n    async init () {\n      try {\n        const res = await this.$axios.post('admin/VipManager/payList', { token: this.token, page: this.page })\n        this.list = res.data.data.list || []\n        this.pageCount = res.data.data.rows || 0\n      } catch (err) {\n        throw (err)\n      }\n    },\n    //维修厂详情\n    async details (row) {\n      try {\n\n        const res = await this.$axios.post('admin/VipManager/paylistDetail', { token: this.token, id: row.id })\n        this.detailsList = res.data.data\n        this.$nextTick(() => {\n          const ViewerDom = this.$refs.images\n          Viewer(ViewerDom)\n        })\n      } catch (err) {\n        throw (err)\n      }\n    },\n    //通过审核\n    async through () {\n      try {\n        const res = await this.$axios.post('admin/ShopAudit/passShop', { token: this.token, sid: this.sid, aid: this.aid })\n        if (res.data.code == 1) {\n          this.detailsVisible = false\n          this.$message({ message: res.data.msg, type: 'success' })\n          this.init()\n        } else {\n          this.$message.error(res.data.msg)\n\n        }\n      } catch (err) {\n        throw (err)\n      }\n    },\n    erAuth () {\n      var id = this.$route.query.id;\n      this.curId = id;\n      this.$axios.post('admin/Auth/erAuth', {\n        token: window.sessionStorage.getItem('bbytoken'),\n        id: id\n      }).then(res => {\n        if (res.data.code == 1) {\n          var arr = res.data.data;\n          for (var i = 0; i < arr.length; i++) {\n            if (arr[i].son) {\n              if (arr[i].name == '支付记录') {\n                this.seCurId = arr[i].id;\n                this.threeAuthList = arr[i].son;\n              }\n\n            }\n          }\n          this.authList = arr;\n        } else {\n          this.$alert(res.data.msg, '提示', {\n            type: 'error'\n          });\n        }\n      })\n        .catch(err => {\n          alert(err);\n        })\n    }\n\n  },\n  mounted () {\n    this.init()\n    this.erAuth()\n  },\n\n\n}\n</script>\n<style scoped>\n.details > div {\n  padding: 8px 0;\n}\n/* .details > div > span:first-child {\n  display: inline-block;\n  width: 95px;\n  text-align: right;\n  margin-right: 10px;\n} */\n\n.images > div {\n  display: inline-block;\n  width: 90px;\n  height: 90px;\n  margin: 0 10px 30px 0;\n  text-align: center;\n}\n.images > div > span {\n  display: inline-block;\n  width: 100%;\n  height: 100%;\n  background-color: #ccc;\n}\n.images img {\n  width: 100%;\n  height: 100%;\n}\n</style>"],"sourceRoot":""}]);

